@use "../../../styles/typography" as *;
@use "../../../styles/variables" as *;

.techSkills {
    .techSkill__tile {
        opacity: 65%;
        .techSkill__image {
            opacity: 75%;
        }
        .techSkill__name {
            color: $color-primary;
            font-size: 1.4rem;
        }
    }
}

.techSkill__tile:hover {
    opacity: 100%;
    .techSkill__image {
        opacity: 92%;
    }
}

@media (max-width: 43rem) {
    .techSkills {
        .techSkill__tile {
            .techSkill__name {
                font-size: 0.8rem;
            }
        }
    }
}
